"use client"

import { useState } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { TiptapEditor } from "@/components/dashboard/tipTapEditor"
import { createPost } from "@/services/postService"
import { toast } from "@/hooks/use-toast"
import { AxiosError } from "axios"

export default function NewPostPage() {
  const [title, setTitle] = useState("")
  const [content, setContent] = useState("<p>Start writing your post here...</p>")
  const [isPublished, setIsPublished] = useState(false)
  const [isSubmitting, setIsSubmitting] = useState(false)
  const router = useRouter()

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsSubmitting(true)
    try {
      await createPost({ title, content, is_published: isPublished })
      toast({
        title: "Успешно",
        description: "Пост успешно создан",
      })
      router.push("/dashboard/posts")
    } catch (error) {
      if (error instanceof AxiosError) {
        console.error("Error creating post:", error)
        toast({
          title: "Ошибка",
          description: error.response?.data.detail || "Не удалось создать пост. Пожалуйста, попробуйте снова.",
          variant: "destructive",
        })
      } else {
        console.error("Error creating post:", error)
        toast({
          title: "Ошибка",
          description: "Не удалось создать пост. Пожалуйста, попробуйте снова.",
          variant: "destructive",
        })
      }
    } finally {
      setIsSubmitting(false)
    }
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle className="text-2xl md:text-3xl">Create New Post</CardTitle>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="title">Title</Label>
            <Input id="title" value={title} onChange={(e) => setTitle(e.target.value)} required />
          </div>
          <div className="space-y-2">
            <Label>Content</Label>
            <TiptapEditor content={content} onChange={setContent} />
          </div>
          <div className="flex items-center space-x-2">
            <input
              type="checkbox"
              id="isPublished"
              checked={isPublished}
              onChange={(e) => setIsPublished(e.target.checked)}
              className="rounded border-gray-300 text-primary focus:ring-primary"
            />
            <Label htmlFor="isPublished">Publish immediately</Label>
          </div>
          <Button type="submit" className="w-full sm:w-auto" disabled={isSubmitting}>
            {isSubmitting ? "Creating..." : "Create Post"}
          </Button>
        </form>
      </CardContent>
    </Card>
  )
}

